/** @format */

.rate-comp {
  display: flex;
  justify-content: space-between;
  width: 98px;
  height: 18px;
  margin-top: 24px;

  @media screen and (min-width: $breakpoint-desktop) {
    width: 196px;
    height: 36px;
  }
}

.rate-container {
  display: flex;
  align-items: flex-start;
}

.star {
  width: 15px;
  height: 15px;

  @media screen and (min-width: $breakpoint-desktop) {
    width: 30px;
    height: 30px;
  }
}
